import { Component, OnInit } from '@angular/core';
import { GaIssuedService, GaIssuedItemService, AssetAssetService, AssetModelxService } from 'src/app/sdk/asset/services';
import { DirektoriPegawaiMasterService, CompanyKantorService, CompanyDepartemenService, CompanyDivisiService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService, NzDrawerService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Issued, Issued_item, Modelx } from 'src/app/sdk/asset/models';
import { Pegawai_master, Kantor, Divisi, Departemen } from 'src/app/sdk/hr/models';
import { FormControl } from '@angular/forms';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';
import { SearchAssetComponent } from 'src/app/components/search-item/search-asset/search-asset.component';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private gaIssuedService: GaIssuedService,
        private gaIssuedItemService: GaIssuedItemService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private companyKantorService: CompanyKantorService,
        private companyDepartemenService: CompanyDepartemenService,
        private companyDivisiService: CompanyDivisiService,
        private assetAssetService: AssetAssetService,
        private assetModelxService: AssetModelxService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private drawerService: NzDrawerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService,
    ) { }

    toogleUpload = false
    dataIssued: Issued = {}
    id_issued: string = ''

    list_item: Issued_item[] = []

    listPegawai: Pegawai_master[] = []
    listKantor: Kantor[] = []
    listDivisi: Divisi[] = []
    listDepartemen: Departemen[] = []

    ngOnInit() {
        this.id_issued = this.activatedRoute.snapshot.paramMap.get('id_issued');
        this.form()
        this.getData()
        this.getListItem()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'ga_issued.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }

    getPreeData() {
        this.listPegawai = []
        this.listKantor = []
        this.listDepartemen = []
        this.listDivisi = []
        if (this.dataForm.value.to_object == 'Employee') {
            this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(data => {
                this.listPegawai = data.data
            })
        }
        if (this.dataForm.value.to_object == 'Office') {
            this.companyKantorService.getCompanyKantors().subscribe(data => {
                this.listKantor = data.data
            })
        }
        if (this.dataForm.value.to_object == 'Departement') {
            this.companyDepartemenService.getCompanyDepartemens().subscribe(data => {
                this.listDepartemen = data.data
            })
        }
        if (this.dataForm.value.to_object == 'Division') {
            this.companyDivisiService.getCompanyDivisis().subscribe(data => {
                this.listDivisi = data.data
            })
        }

    }

    getData() {
        this.spinner.show()
        this.gaIssuedService.getGaIssuedsId(this.id_issued).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.spinner.hide()

                this.singleUploadService.param.next({
                    for_module: "ga_issued",
                    for_table: "erp_asset_ga.issued",
                    for_module_id: data.data.id_issued

                })

            }
        )
    }

    getListItem() {
        let param = {
            id_issued: this.id_issued
        }
        this.gaIssuedItemService.getGaIssuedItems(JSON.stringify(param)).subscribe(
            data => {
                this.list_item = data.data
            }
        )
    }

    id_asset: string = null;
    add_item() {
        const drawerRef = this.drawerService.create<SearchAssetComponent, { value: string }, string>({
            nzTitle: 'Search Asset',
            nzContent: SearchAssetComponent,
            nzWidth: '900px',
            nzContentParams: {
                value: 'arifan'
            }
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {

            if (typeof data === 'string') {
                this.id_asset = data;
                this.beforeAsset()

            }
        });
    }

    dataItem: any = {};

    beforeAsset() {
        console.log("panggie ko")
        this.assetAssetService.getAssetAssetsId(this.id_asset).subscribe(
            data => {
                this.dataItem.sn = data.data.sn;
                this.dataItem.id_asset = data.data.id_asset;
                this.dataItem.qty = 1;
                this.id_model = data.data.id_model
                console.log(this.dataItem)
                this.getModel()
            }
        )
    }

    id_model: string = ''
    model: Modelx = {}
    getModel() {
        console.log("panggie ko")
        this.assetModelxService.getAssetModelxesId(this.id_model).subscribe(
            data => {
                this.model = data.data
            }
        )
    }

    note: string = ''

    simpan_item() {
        if (this.dataItem.qty < 1) {
            alert('tidak boleh kosong');
            return false;
        }

        let param: Issued_item = {
            id_issued: this.id_issued,
            qty: this.dataItem.qty,
            note: this.dataItem.harga_satuan,
            id_asset: this.id_asset
        };

        this.spinner.show()
        //simpan ka server
        this.gaIssuedItemService.postGaIssuedItems(param).subscribe(
            data => {
                this.id_model = null;
                this.spinner.hide();
                this.getListItem()
            },
            err => {
                this.spinner.hide();
                this.pesan.pesanWarningForm('Dupikat pembelian Item!')
            }
        )
    }

    deleteItem(id) {
        this.spinner.show()
        this.gaIssuedItemService.deleteGaIssuedItemsId(id).subscribe(
            data => {
                this.getListItem()
                this.spinner.hide()
            }
        )
    }


    dataForm: FormGroupTypeSafe<Issued>
    form() {
        this.dataForm = this.fb.group<Issued>({
            to_id: new FormControl(''),
            to_object: new FormControl(''),
            tanggal_issued: new FormControl(''),
            no_issued: new FormControl(''),
            id_user: new FormControl(''),
            id_data_flow: new FormControl(''),
            id_data_flow_detail: new FormControl(''),
            id_dokumen: new FormControl(''),
            id_issued: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            posisi: new FormControl(''),
            status: new FormControl(''),
            description: new FormControl(''),
            validasi: new FormControl(''),
        })
    }


    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }


        this.dataIssued = this.dataForm.value
        this.gaIssuedService.putGaIssuedsId({ id: this.id_issued, body: this.dataIssued }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/asset/management/issued/list'])
    }


}
