import { Component, OnInit } from '@angular/core';
import { GaIssuedService, GaIssuedItemService } from 'src/app/sdk/asset/services';
import { DirektoriPegawaiMasterService, CompanyKantorService, CompanyDepartemenService, CompanyDivisiService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { ToolsLogFlowService } from 'src/app/sdk/user/services';
import { StandarApprovalService, standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { Issued, Issued_item } from 'src/app/sdk/asset/models';
import { Pegawai_master, Kantor, Divisi, Departemen } from 'src/app/sdk/hr/models';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private gaIssuedService: GaIssuedService,
        private gaIssuedItemService: GaIssuedItemService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private companyKantorService: CompanyKantorService,
        private companyDepartemenService: CompanyDepartemenService,
        private companyDivisiService: CompanyDivisiService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private toolsLogFlowService: ToolsLogFlowService,
        private standarApprovalService: StandarApprovalService,
    ) { }

    dataIssued: Issued = {}
    list_item: Issued_item[] = []
    id_issued: string = ''

    dataPegawai: Pegawai_master = {}
    dataKantor: Kantor = {}
    dataDivisi: Divisi = {}
    dataDepartemen: Departemen = {}

    ngOnInit() {
        this.activatedRoute.paramMap.subscribe(
            params => {
                console.log(params.get('id_issued'))
                if (params.get('id_issued')) {
                    this.id_issued = params.get('id_issued')
                }

                this.getDetail()
                this.getDetailItem()
                this.getLog()

            }
        )

        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getDetail()
                    this.getDetailItem()
                    this.getLog()
                }
            }
        )


    }
    params: standarFlow = null;
    id_data_flow: string = ''
    getDetail() {
        this.spinner.show()
        this.gaIssuedService.getGaIssuedsId(this.id_issued).subscribe(
            data => {
                this.dataIssued = data.data;
                this.getPegawai(data.data.to_id)
                this.getkantor(data.data.to_id)
                this.getDepartemen(data.data.to_id)
                this.getDivisi(data.data.to_id)
                this.spinner.hide()

                this.params = {
                    id: data.data.id_issued,
                    id_data_flow_detail: data.data.id_data_flow_detail,
                    id_data_flow: data.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_issued"
                }
                this.standarApprovalService.param.next(this.params)
                this.spinner.hide()

            }
        )
    }


    getPegawai(to_id) {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMastersId(to_id).subscribe(
            data => {
                this.dataPegawai = data.data;
            }
        )
    }
    getkantor(to_id) {
        this.companyKantorService.getCompanyKantorsId(to_id).subscribe(
            data => {
                this.dataKantor = data.data;
            }
        )
    }
    getDivisi(to_id) {
        this.companyDivisiService.getCompanyDivisisId(to_id).subscribe(
            data => {
                this.dataDivisi = data.data;
            }
        )
    }
    getDepartemen(to_id) {
        this.companyDepartemenService.getCompanyDepartemensId(to_id).subscribe(
            data => {
                this.dataDepartemen = data.data;
            }
        )
    }

    getDetailItem() {
        this.gaIssuedItemService.getGaIssuedItems(JSON.stringify({ id_issued: this.id_issued })).subscribe(
            res => {
                this.list_item = res.data;
                this.spinner.hide()
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_issued })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }

    delete(id) {
        this.spinner.show()
        this.gaIssuedService.deleteGaIssuedsId(id).subscribe(data => {
            this.goToBack()
            this.spinner.hide()
        },
            err => {
                this.pesan.pesanWarningForm('Data tidak boleh di delete')
                this.spinner.hide()
            },
        )
    }

    goToBack() {
        this.router.navigate(['/asset/management/issued/list'])
    }

}
